body,
input {
  font-family: 'Playfair Display', serif;
  background-color: #282a36;
}

.container {
  width: 100vw;
  height: 100vh;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 7rem;
  padding: 0.2rem;
}

.img{
  display: flex;
  justify-content: center;
  align-items: center;
}

.img img{
  width: 500px;
}

.login-container{
  display: flex;
  align-items: center;
  text-align: center;
}

form {
  width: 360px;
}

.avatar{
  width: 100px;
}

form h2{
  font-size: 3rem;
  text-transform: uppercase;
  margin: 15px 0;
  color: rgb(248, 242, 242);
}

.input-div{
  display: grid;
  grid-template-columns: 7% 93%;
  margin: 25px 0;
  padding: 5px 0;
  /* border-bottom: 2px solid #d9d9d9; */
}

.i{
  display: flex;
  justify-content: center;
  align-items: center;
}
.i i{
  color: #d9d9d9
}


 .input-div > div h5{
  position: absolute;
  left: 10px;
  top: 50%;
  transform: translateY(-50%);
  color: #999;
  font-size: 18px;
  transition: .3s;

}

