a {
  display: block;
  text-align: center;
  text-decoration: none;
  color: #999;
  font-size: 0.9rem;
  transition: .3s;

}

.btn {
  display: block;
  width: 250px;
  height: 45px;
  padding: 10px;
  border-radius:  10px;
  margin: 1rem  3rem;
  margin-left: 20%;
  font-size: 1.2rem;
  outline: none;
  border: none;
  /* background-image: linear-gradient(to right, #32be8f, #38d39f, #32be8f); */
  cursor: pointer;
  color: #fff;
  font-family: 'Poppins', sans-serif;
  background-size: 200%;
  transition: .5s;
}

@media screen and (max-width: 1050px){
  .container {
    grid-gap: 5rem;
  }
}

@media screen and (max-width: 1000px){
  form {
    width: 290px;
  }
  form h2{
    font-size: 2.4rem;
    margin: 8px 0;
  }

  .img img{
    width: 400px;
  }

  .btn {
    margin-left: auto;
  }
}

@media screen and (max-width: 900px){
  .img {
    display: none;
  }
  .container {
    grid-template-columns: 1fr;
  }
  .login-container {
    justify-content: center;
  }
  .btn {
    margin-left: 1.7rem;
  }
}